.block--psk12 .col-lg-12 .infographic .info-wrapper {
  padding: 15px 0;
}
.block--psk12 .col-lg-12 .infographic .info-wrapper.info--0 {
  padding: 0;
}
.block--psk12 .col-lg-12 .infographic__svg {
  max-width: 90%;
  margin: auto;
}
@media screen and (max-width: 1024px) {
  .block--psk12 .info-wrapper {
    padding-top: 20px;
  }
  .block--psk12 .info-wrapper.info--0 {
    padding-top: 0;
  }
}